body {
  line-height: 1.5;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container-fluid .logo {
  border-radius: 50%;
  transition: all 0.3s ease;
}

.container-fluid .navbar-nav {
  text-align: center;
}
.navbar-nav > li {
  margin-left: 30px;
  margin-right: 30px;
}
.container-fluid .cart-links a {
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  text-align: center;
  margin: 0 30 30 0;
  line-height: 40px;
  border-radius: 10%;
  color: #ffffff;
  transition: all 0.5s ease;
}
.container-fluid .cart-links a:hover {
  color: #24262b;
  background-color: #ffffff;
}
.container-fluid .btn-outline-light {
  font-weight: 450;
}

.d-flex .login {
  width: 80px;
}
.d-flex .searchbar {
  width: 30vw;
}
.container-fluid .title {
  font-family: "Playfair Display", serif;
}
